body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root{
  --background-color: #E9E9E5;
  --background-color-secondary: #c5c5c2;
  --highlight-color: rgb(245, 104, 65);
  --white-text: whitesmoke;
  --dark-accent: #252424;
  --passed-color: #79e479;
}


body{
  background-color: #DDDDD8;
  text-align: center;

  font-size: 1.5em;
}


.App{
  width: 90%;
  margin: 0 auto 3em;
}

/* TEST CSS */




#copy-link{
  width: 90%;
  height: 5em;
  margin: 0 auto;
}








textarea{
  font-size: 1.5em;
  width: 100%;
}

input[type = 'checkbox']{
    /* Double-sized Checkboxes */
    -ms-transform: scale(2); /* IE */
    -moz-transform: scale(2); /* FF */
    -webkit-transform: scale(2); /* Safari and Chrome */
    -o-transform: scale(2); /* Opera */
    padding: 10px;
    margin-right: 1em;
}


.tutorial-section h1{
  display: inline;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}


.tutorial-container{
    width: 85%;
    margin: 0 auto;
    text-align: left;
}

.tutorial-banner{
    display: grid;
    grid-template-columns: 4em auto;
    text-align: left;
    margin: 1em auto;

    cursor: pointer;

}

.tutorial-banner-element{
    box-shadow: 2px 2px 5px var(--dark-accent);
}

.tutorial-active-indicator{
    /* margin: 0;
    font-size: 3em; */
    text-align: center;
    font-weight: 900;
    background-color: var(--dark-accent);
    color: var(--white-text);

    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
}

.content-container{
    box-shadow: 2px 2px 20px var(--dark-accent) inset;
    background-color: whitesmoke;
    border-radius: 8px;
    padding: .25em 0.5em 1em 1.5em;
}

.tutorial-banner-title{
    padding-left: 1em;
    background-color: var(--background-color-secondary);
}

.tutorial-section-heading{
    background-color: var(--background-color-secondary);
}

.toggle-expand{
    background-color: var(--dark-accent);
    color: var(--white-text);
}



.heading-container{
    margin: 0;
    padding-bottom: 0.25em;
    border-bottom: 1px solid var(--dark-accent);
}

.heading-container h2{
    margin: 1em 0 0;
}

.heading-container hr{
    color: red;
}


.embeded-video{
    width: 100%;
    max-width: 560px;
    margin: 0 auto;
}



#assignment-container{
    background-color: var(--background-color);

    padding: 2em;
    width: 85%;
    margin: 2em auto;
    border-radius: 4px;
    box-shadow: 2px 2px 20px rgb(218, 218, 218) inset;
}

