.nf-assignment-score{
    box-shadow: 2px 2px 20px #a08e7a;
    height: 65em;
}

.nf-answer-score{
    display: hidden;
}

#error-navigation-buttons{
    background-color: var(--background-color-secondary);
    border-radius: 10px;
    width: 50%;

    margin: 0 auto;
    
}

#error-navigation-buttons h2{
    color: var(--white-text);
    background-color: var(--dark-accent);
    padding: .25em 0;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;

    margin: 0;
}


.assignment-score-passed{
    box-shadow: 2px 2px 40px var(--passed-color);
  }





#submit-assignment{
    background-color: var(--background-color-secondary);
    padding: .66em;
}

#submit-assignment h2{
    color: var(--highlight-color);
}


#main-nav-banner{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  
    background-color: var(--dark-accent);
    width: 100%;
    margin: 0 auto;
    padding: 2.5em 0;
    text-align: center;
}


#main-nav-banner a{

/* font-size: 1.5em; */
    color: var(--white-text);
    border: 2px solid var(--white-text);

    text-decoration: none;
    font-weight: 600;

    border-radius: 8px;
    padding: .66em;
}

#main-nav-banner a:hover{
    color: var(--highlight-color);
    border-color: var(--highlight-color);
}
input, select, textarea, button{
    border-radius: 4px;
    border: none;

    font-size: 1.5em;
    
    margin: 0.25em 0;

    color: var(--dark-accent);
}

textarea{
    resize: none;
    padding: .66em 0;
    box-shadow: 2px 2px 10px var(--dark-accent) inset;
}





input[type="submit"], #assignment-container, button{
    background-color: rgba(0, 0, 0, 0);
    border: 2px solid black;
    
    padding: 0.25em;
    margin: 1em;
    box-shadow: 2px 2px 8px var(--dark-accent);
    
    cursor: pointer;
}

input[type="submit"]:hover, #assignment-container, button:hover{
    color: var(--highlight-color);
    border: 2px solid var(--highlight-color);
    box-shadow: 5px 5px 20px var(--dark-accent);
}








input[type="text"], input[type="number"]{
    box-shadow: 2px 2px 8px var(--dark-accent);
    text-align: center;
}

select{
    cursor: pointer;
    box-shadow: 2px 2px 8px var(--dark-accent);
}

label{
    font-size: 1.5em;
    margin-bottom: 1em;
}


.copy-text-button{
    font-size: .75em;
}

.assignment-form-section, .section-box{
    padding: 0 0 .66em;
    width: 66%;
    min-width: 300px;
    margin: 1em auto;
    
    border-radius: 10px;
    box-shadow: 2px 2px 10px var(--dark-accent) inset;
    background-color: var(--background-color-secondary);
}

.new-assignment-section-heading, .section-heading{
    margin: 0;
    padding: 0 0 .25em;
    background-color: var(--dark-accent);
    color: var(--background-color);
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}

.in-text-link{
    color: var(--highlight-color);
}
